@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@400;700&family=Righteous&display=swap);
.welcome {
    width: 100%;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    /* border: 1px solid black; */
}

.welcome-logo{
    width: 136px;
    height: 161px;
}

.welcome-logo img {
    width: inherit;
    height: inherit;
}

.welcome-title {
    color: #ffffff;
    font-family: 'Righteous', cursive;
    font-size: 36px;
    font-weight: 400;
    line-height: 44.7px;
}
.container-questions {
    display: flex;
    flex-direction: column;
    margin: 10px auto 10px auto;
    margin-bottom: 80px;
}

.titles {
    background-color: #ffffff;
    height: 65px;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 50px auto;
    border-radius: 5px;
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    cursor: pointer;
}

.titles p {
    margin-left: 15px;
    font-weight: 700;
}

.riscado /*esqueci isso em inglÊs, desculpa*/ {
    text-decoration: line-through;
}

.red {
    color: #FF3030;
}


.orange {
    color: #FF922E;
}

.green {
    color: #2FBE34;
}

ion-icon[name="close-circle"] {
    color: #FF3030;
}

ion-icon[name="help-circle"] {
    color: #FF922E;
}

ion-icon[name="checkmark-circle"] {
    color: #2FBE34;
}

.titles ion-icon {
    font-size: 30px;
    margin-right: 15px;
}

.answers {
    transition: all 5s;
    background-color: #FFFFD4;
    min-height: 135px;
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 50px auto;
    border-radius: 5px;
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    position: relative;

}

.questions {
    transition: all 5s;
    transition-delay: 2s;
    background-color: #FFFFD4;
    min-height: 135px;
    width: 80%;
    display: flex;
    margin: 50px auto;
    border-radius: 5px;
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    position: relative;
}

.questions p,
.answers p {
    margin: 10px 20px;
}

.questions img {
    position: absolute;
    right: 3%;
    bottom: 10%;
    cursor: pointer;
}

.options {
    margin-top: 30px;
    display: flex;
    justify-content: space-evenly;
    color: #FFFFFF;
    font-size: 12px;
    line-height: 14.4px;
    font-weight: 400;
}

.options .redOption{
    width: 85px;
    height: 37px;
    background-color: #FF3030;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
}

.options .orangeOption{
    min-width: 85px;
    height: 37px;
    background-color: #FF922E;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
}

.options .greenOption{
    width: 85px;
    height: 37px;
    background-color: #2FBE34;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
}



.hidden {
    display: none;
}
.top {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    margin: 10px auto;
}

.logo {
    width: 52px;
    height: 60px;
}

.logo img {
    width: inherit;
    height: inherit;
}


/*  igual a classe welcome-title . 
    depois verificar o que pode simplificar
*/
.title {
    color: #FFFFFF;
    width: 203px;
    height: 45px;
    font-family: 'Righteous', cursive;
    font-size: 36px;
    font-weight: 400;
    line-height: 44.7px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 70px;
    max-height: 244px;
    background-color: #FFFFFF;
    font-weight: 400;
    color: #333333;
    font-size: 18px;
    line-height: 21.6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.05);
    padding-bottom: 10px;
}

.bottom-icons {
    font-size: 30px;
}


.title-result {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    margin: 10px auto;
}

.title-result img {
    margin: 0 8px;
}

.text-result {
    margin: 10px auto;
}

.reset-button {
    background-color: #FB6B6B;
    width: 167px;
    height: 32px;
    color: #FFFFFF;
    font-size: 14px;
    line-height: 16.8px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    margin: 15px auto;
    cursor: pointer; 
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
    background-color: #FB6B6B;
    font-family: 'Recursive', sans-serif;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

